import { createLexer, TokenMap } from './common';

function getTokens(input) {
  const lexer = createLexer(input); // 捕获词法错误

  const errors = [];
  lexer.removeErrorListeners();
  lexer.addErrorListener({
    syntaxError(_1, _2, _3, charPositionInLine) {
      errors.push(charPositionInLine);
    }

  }); // 获取token流

  const tokens = lexer.getAllTokens();
  console.log(tokens);
  const res = tokens.map(token => {
    const type = lexer.ruleNames[token.type - 1];
    const typeName = TokenMap[type] || TokenMap.UnexpectedCharacter;
    return {
      scopes: typeName,
      startIndex: token.charPositionInLine
    };
  }); // 将捕获到的错误加入res中

  errors.forEach(point => res.push({
    scopes: 'error',
    startIndex: point
  }));
  return res;
}

function tokenForLine(input) {
  const tokens = getTokens(input);
  return {
    tokens,
    endState: new State()
  };
}

class State {
  clone() {
    return new State();
  }

  equals(other) {
    return true;
  }

}

export class TokensProviders {
  tokenize(line, state) {
    return tokenForLine(line);
  }

  getInitialState() {
    return new State();
  }

}